.checkout {
	@media (min-width: 1025px) {
		width: 85.3rem;
	}

	// .checkout__title

	&__title {
		&:not(:last-child) {
			margin-bottom: var(--gap);

			@media (max-width: 500px) {
				margin-bottom: 3rem;
			}
		}
	}
}

.checkout-form {
	// .checkout-form__item

	&__item {
		&:not(:last-child) {
			margin-bottom: var(--gap);

			@media (max-width: 500px) {
				margin-bottom: 3rem;
			}
		}
	}

	// .checkout-form__item-title

	&__item-title {
		@extend %text-20;
		font-weight: 500;

		&:not(:last-child) {
			margin-bottom: 3rem;

			@media (max-width: 500px) {
				margin-bottom: 2rem;
			}
		}
	}

	// .checkout-form__radios

	&__radios {
		display: grid;
		grid-gap: 1.5rem;
	}

	// .checkout-form__item-radio

	&__item-radio {
		display: flex;
		align-items: flex-start;
	}

	// .checkout-form__comment

	&__comment {
		width: 100%;
		height: 9rem;
		transition: height 0.5s ease;
		@include scrollbar;

		&:focus,
		&--filled {
			height: 18rem;
			transition: $transition height;
		}

		@media screen and (min-width: 768px) {
			height: var(--gap);

			&:focus,
			&--filled {
				height: 12rem;
			}
		}
	}
}

.personal-data-form {
	// .personal-data-form__inputs

	&__inputs {
		display: grid;
		grid-gap: 3rem;

		@media (max-width: 500px) {
			grid-gap: 2rem;
		}

		@media (min-width: 501px) {
			grid-template-columns: repeat(2, 1fr);
		}

		@media (min-width: 768px) {
			grid-template-columns: repeat(3, 1fr);
		}
	}

	// .personal-data-form__input

	&__input {
		>* {
			width: 100%;
		}

		&:last-child {
			@media (min-width: 501px) {
				grid-column: span 2;
			}
		}
	}
}

.checkout-radio {
	border: 0.1rem solid #c0c0c0;
	padding: 3rem;
	width: 100%;
	cursor: pointer;

	@media (max-width: 500px) {
		padding-left: 1.5rem;
		padding-right: 1.5rem;
	}

	&--checked {
		border-color: var(--green);
	}

	.radio-style {
		position: relative;
		width: 2.4rem;
		height: 2.4rem;
		border-radius: 50%;
		flex-shrink: 0;
		border: 0.1rem solid #c0c0c0;
	}

	.radio-box:checked+.radio-style {
		border-color: var(--green);

		&:before {
			content: '';
			position: absolute;
			@extend %pos-center-xy;
			width: 1rem;
			height: 1rem;
			border-radius: 50%;
			background-color: var(--green);
		}
	}

	// .checkout-radio__content

	&__content {
		width: 100%;
		display: grid;
		grid-gap: 2rem;
		margin-left: 2rem;

		@media (min-width: 501px) {
			justify-items: flex-start;
		}

		@media (max-width: 500px) {
			margin-left: 1rem;
		}

		>* {
			width: 100%;
		}
	}

	// .checkout-radio__title

	&__title {
		@extend %text-16;
		font-weight: 600;
	}

	// .checkout-radio__list

	&__list {
		margin-top: 1.6rem;

		>div {
			display: flex;
			flex-wrap: wrap;

			&:not(:last-child) {
				margin-bottom: 0.3rem;
			}
		}

		dt {
			float: left;
			padding-right: 0.3rem;
			color: var(--gray);
		}
	}

	// .checkout-radio__hidden

	&__hidden {
		display: none;
	}

	// .checkout-radio__inputs

	&__inputs {
		display: grid;
		grid-gap: 2rem;

		@media (min-width: 501px) {
			width: 23rem * 2+2rem;
			grid-template-columns: repeat(2, 1fr);
		}
	}

	&__input {
		>* {
			width: 100%;
		}
	}

	// .checkout-radio__btn

	&__btn {
		display: inline-block;
		padding: 1.4rem 3.8rem;
		font-weight: 600;
	}

	&--without-border {
		padding: 0;
		border: none;

		.checkout-radio__content {
			grid-gap: 1rem;
		}
	}
}

.checkout-delivery {
	&__data {
		width: 100%;
		display: grid;
		grid-gap: 2.4rem 3rem;

		@media (max-width: 500px) {
			grid-gap: 2rem;
		}

		@media (min-width: 768px) {
			grid-template-columns: 24.2rem 1fr;
		}
	}

	&__btn {
		font-weight: 500;
		padding: 1.35rem;
	}

	&__label {
		@extend %text-13;
		font-weight: 600;
		display: block;

		&:not(:last-child) {
			margin-bottom: 1rem;
		}
	}

	&__date {
		small {
			@extend %text-12;
			display: block;
			margin-top: 0.8rem;
		}
	}

	&__input {
		width: 100%;
	}
}
